<template>
  <div class="content">
    <div class="top-title">邀请注册</div>
    <div v-if="isShow" class="mask-layer">
      <div class="content">
        <div style="font-size:14px;height:35rem;overflow-y: auto;padding: 1rem">
          <p style="font-weight:bold;">感谢您注册：</p><p>请核实您是否满足以下条件：（若不满足无法审核通过哦）</p>
          <p>1.淘气值满500</p><p>2.买家信誉满3心</p><p>3.周点数<3</p><p>4.tb号注册满两年以上</p><p>5.照妖镜查号无负面记录</p>
          <p>注册过程需要提交身份证正反面哦，（承诺资料保密，仅作实名认证用）</p>
          <p style="color:red;">重要提醒-四不要</p>
          <p style="color:red;">您的徒弟（孙）不要用同一个设备进行任务！</p>
          <p style="color:red;">您的徒弟（孙）不要在同一个IP（wifi）下进行任务！</p>
          <p style="color:red;">您的徒弟（孙）不要和你用相同姓名的银行卡！</p>
          <p style="color:red;">您的徒弟（孙）不要用和您一样或者接近的收货地址！</p>
          <p style="color:red;">如果因以上原因被商家撤单，将扣除对应的佣金</p>
        </div>
        <van-button class="btn" type="default" size="large" @click="continueGo">{{verifyText}}</van-button>
      </div>
    </div>

    <van-cell-group>
      <van-field v-model="user.name" label="姓名" required placeholder="请输入姓名"/>
      <van-field v-model="user.mobile" label="手机号" required placeholder="请输入手机号"/>
      <van-field v-model="user.taobaoUserId" label="淘宝号" required placeholder="请输入淘宝号"/>
      <van-field
        label="开户银行"
        required
        v-model="user.bank"
        placeholder="请选择开户银行"
        readonly
        @click="showBank = true"
      />
      <van-field v-model="user.bank_number" label="卡号" required placeholder="请输入银行卡号"/>
      <van-field v-model="user.id_card" label="身份证号" required placeholder="请输入身份证号"/>
      <van-cell-group>
        <!--confirm   点击右上方完成按钮   一个数组参数，具体格式看下方数据格式章节
      cancel    点击取消按钮时
      change    选项改变时触发 参考Picker 实例，所有列选中值，当前列对应的索引-->
        <van-field v-model="areaText" label="省/市/区" required @click="showArea = true" placeholder="请选择所在地区"/>
        <van-popup v-model="showArea" position="bottom">
          <van-area
            ref="area"
            value="110000"
            :area-list="areaList"
            @change="onChangeAdd"
            @confirm="showArea = false"
            @cancel="showArea = false"
          />
        </van-popup>
      </van-cell-group>
      <van-field v-model="user.age" label="年龄" required placeholder="请输入您的年龄"/>
      <van-field
        label="性别"
        required
        v-model="sexText"
        placeholder="请选择性别"
        readonly
        @click="showSex = true"
      />
      <van-field
        v-model="user.password"
        label="登录密码"
        required
        type="password"
        placeholder="请输入6位以上数字或字母"
      />
      <van-field
        v-model="user.repassword"
        label="确认密码"
        required
        type="password"
        placeholder="请输入确认密码"
      />
      <mobileCode :mobile="user.mobile" v-bind:scene="`register`"  ref="mobileCode"/>
    </van-cell-group>
    <uploadNew v-if="isUpload" @setFiles = "setFiles" ref="uploadNew"/>
    <van-cell-group style="padding:10px;">
      <van-button :loading="isSubmit" @click="subRegister" type="danger" :bottom-action="true">提交审核</van-button>
    </van-cell-group>
    <van-popup v-model="showBank" position="bottom">
      <van-picker
        show-toolbar
        :columns="bankColumns"
        title="选择银行"
        @cancel="showBank = false"
        @confirm="onBankConfirm"
      />
    </van-popup>
    <van-popup v-model="showSex" position="bottom">
      <van-picker
        show-toolbar
        :columns="sexColumns"
        title="选择性别"
        @cancel="showSex = false"
        @confirm="onSexConfirm"
      />
    </van-popup>
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field,
  Area,
  Picker
} from "vant";

import { CONF, VERIFY_INVITE } from "@/api/user";
import { idCard } from "@/assets/js/regexp";
import uploadNew from "./upload-new";
import mobileCode from "@/common/mobileCode";
import bankList from "../../../assets/data/bank";
import areaLists from "../../../assets/data/area";
import { REGISTER, USER_SENDCODE, COMMON_CONF } from "@/api/user";
import validateMobile from "vant/lib/utils/validate/mobile";

export default {
  name: "regInvite",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview,
    [uploadNew.name]: uploadNew,
    [mobileCode.name]: mobileCode,
    [Area.name]:Area,
    [Picker.name]: Picker,
  },
  props: {
    invite_code: '',
    auth_time: '',
    auth_code: ''
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      isUpload:true, //是否需要 上次身份图片
      isSubmit:false,
      showBank: false,
      showArea:false,
      showSex:false,
      sexText:'',
      bankColumns:[],
      sexColumns: [
        {
          value: "1",
          text: "男"
        },
        {
          value: "2",
          text: "女"
        },],
      user:{
        name: '',
        mobile: '',
        taobaoUserId:'',
        bank: '',
        bank_number: '',
        id_card:'',
        age:'',
        sex:'',
        address:'',
        password: '',
        repassword: '',
        v_code: ''
      },
      isShow: false,
      verifyText: '',
      countSeconds: 0,
      files:'',
      areaText: '',
      areaList: []
    };
  },
  methods: {
    initPage(){
      this.$reqGet(COMMON_CONF)
      .then(res => {
        let data = res.data.data;
        if(data.register_open != 1){
          Dialog.alert({
            title: "提示",
            message: "邀请链接已失效" // 注册通道关闭
          }).then(() => {
            this.$router.push({
              name: "login",
            });
          });
        }
      }).catch((err) => {
      })
      if (!this.countSeconds) {
        this.countSeconds = 3;
        this.countDown();
      }
      this.bankColumns = bankList['bank_list']
      this.areaList = areaLists
      // 邀请验证
      this.$reqPost(VERIFY_INVITE,{
        invite_code: this.invite_code
      }).then(res => {
        let data = res.data.data
        if(data.invite_auth != 1){
          Dialog.alert({
            title: "提示",
            message: "邀请链接已失效"
          }).then(() => {
              this.$router.push({
                name: "login",
              });
          });
        }
      }).catch((err) => {
      })
    },
    onChangeAdd (picker, value, index) {
      //因为解析过来的数据是数组而areaText定义字符串
      let areaName = ''
      let areaCodes = '';
      for (var i = 0; i < value.length; i++) {
        areaName = areaName + value[i].name + ' '
        areaCodes = areaCodes + value[i].code + ','
      }
      this.areaText = areaName
      this.user.address = areaCodes
    },
    onSexConfirm(value) {
      this.sexText = value.text;
      this.user.sex = value.value;
      this.showSex = false;
    },
    setFiles(file){
      this.files = file
    },
    //提交注册
    subRegister(){
      try {
          let _this = this;
          //验证合法性
          let items = ["name","mobile",'taobaoUserId',"bank","bank_number","id_card","address","age","sex","invite_code","password","repassword"]
          items.forEach(function(item,index){
            let msg =_this.validate(item)
            if(msg && msg != ''){
              throw new Error(msg)
            }
          })

          let formdata = new FormData();
          if(this.isUpload){
            if(!this.files.upFile){
              throw new Error('请上传您的身份证正面哦')
            }
            if(!this.files.downFile){
              throw new Error('请上传您的身份证反面哦')
            }
            if(!this.files.alipayFile){
              throw new Error('请上传支付宝截图')
            }
            if(!this.files.bankFile){
              throw new Error('请上传银行卡截图')
            }
            if(!this.files.poseFile){
              throw new Error('请拍摄认证视频')
            }
            formdata.append("front", this.files.upFile.file);
            formdata.append("reverse", this.files.downFile.file);
            formdata.append("pose", this.files.poseFile.file);
            formdata.append("alipayPic", this.files.alipayFile.file);
            formdata.append("bankPic", this.files.bankFile.file);
            formdata.append("poseEg", this.files.poseEgName);
          }

          formdata.append("name", this.user.name)
          formdata.append("mobile", this.user.mobile)
          formdata.append("taobaoUserId", this.user.taobaoUserId)
          formdata.append("bank", this.user.bank)
          formdata.append("bank_number", this.user.bank_number)
          formdata.append("card_no", this.user.id_card)
          formdata.append("password", this.user.password)
          formdata.append("sex", this.user.sex)
          formdata.append("age", this.user.age)
          formdata.append("address", this.user.address)
          formdata.append("areaText", this.areaText)
          formdata.append("v_code", this.$refs.mobileCode.v_code)

          formdata.append("invite_code", this.invite_code) //邀请注册码
          formdata.append("auth_time", this.auth_time) //链接生成时间
          formdata.append("auth_code", this.auth_code) //注册授权验证码
          formdata.append("reg_type", 2) //邀请注册
          this.isSubmit = true
          this.$reqPost(REGISTER, formdata).then(res => {
              this.isSubmit = false
              this.$router.push({ name: "follow"});
          }).catch(err => {
              this.isSubmit = false
          });
      } catch (e) {
        Toast(e.message)
      }
    },
    //验证
    validate(key) {
      const value = this.user[key];
      switch (key) {
        case "name":
          return value ? "" : "请填写真实姓名";
        case "mobile":
          if(!value) return "请填写手机号码";
          return validateMobile(value) ? "" : "请输入正确的手机号码";
        case "taobaoUserId":
          return value ? "" : "请输入淘宝账号";
        case "id_card":
           return idCard(value) ? '' : '请输入15位或18位身份证号！';
        case "bank":
          return value ? "" : "请选择银行";
        case "bank_number" :
          return value ? "" : "请填写银行卡号";
        case "address" :
          return value ? "" : "请选择所在地区";
        case "age" :
          return value ? "" : "请填写年龄";
        case "sex" :
          return value ? "" : "请选择性别";
        case "password" :
          return value ? "" : "请填写密码";
        case "repassword":
          let msg = value ? "" : "请再次输入密码";
          if (!msg) {
            msg = (value == this.user["password"]) ? "" : "两次输入的密码不一致";
          }
          return msg;
        case "v_code" :
          return value ? "" : "请填写验证码";
        default :
          return '';
      }
    },
    //银行选择确认
    onBankConfirm(value, index) {
      this.showBank = false;
      this.user.bank = value.text;
    },
    //返回
    goBack() {
      this.$router.push({name: 'index'});
    },
    countDown() {
      if (this.countSeconds === 0) {
        this.verifyText = "继续";
        return;
      } else {
        this.verifyText = `${this.countSeconds--}秒`;
      }
      setTimeout(() => {
        this.countDown();
      }, 1000);
    },
    continueGo() {
      if(this.countSeconds <=0 ){
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    },
    setVcode(code){
      this.user.v_code = code
    }
  }
};
</script>
<style lang="scss" scoped>
.top-title {
  text-align: center;
  padding: 12px;
  background-color: #fff;
  font-size: 16px;
}
.tip-r {
  color: red;
}
.tip_message{
  font-size: 12px;
}
.mask-layer {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.75);
  position: fixed;
  top: 0;
  z-index: 999;
  left: 0;
  .content {
    width: 80%;
    min-height: 80%;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &>div {
      box-sizing: border-box;
      padding: 20px 15px 0 15px;
      p {
        line-height: 1rem;
      }
    }
    .btn {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
</style>



